* {
    box-sizing: border-box;
}

.header_container {
    display: grid;
    background-color: whitesmoke;
    height: auto;
    // padding: 1em;
    width: 100vw;
    grid-template-columns: 1fr repeat(3, minmax(100px, 300px));
    grid-gap: 2em;
    margin: 1em 20%;
}

.logo_header {
    grid-column-start: 0;
    grid-column-end: 1;
    width: 400px;
}

header > button {
    color: green;
}

.button_support {
    grid-column-start: 1;
    grid-column-end: 2;
}
.button_support {
    grid-column-start: 2;
    grid-column-end: 3;
}

.search_bar {
    grid-column-start: 3;
    grid-column-end: 4;
}